.Admin{
    width: 100%;
    display: flex;
    &__sitebar{
        width: 300px;
        height: auto;
        display: flex;
        background-color:#016BFF;
        flex-direction: column;
        align-items: center;
        border-right: 3px solid rgb(150, 144, 144);
        &__logo{
            color: aliceblue;
        }
        &__nav{
            width: 100%;
            text-align: start;
            
            &__item{
                display: inline-block;
                text-decoration: none;
                list-style: none;
                margin-left: 60px;
                margin-bottom: 30px;
                color: white;
                font-size: 18px;
                
                img{
                    margin-right: 20px;
                }
                &:hover{
                   color: rgb(122, 125, 129);
                }
                &:active{
                    color: black;
                }
            }
        }
        
    }

    &__panel{
        display: flex;
        flex-direction: column;
        width: 100%;
      &__header{
        width: 100%;
        padding: 10px 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        border-bottom: 2px solid rgb(227, 222, 222);
        &__input{
            width: 400px;
            height: 50px;
           border: none;
           font-size: 20px;
           border-bottom:3px solid rgb(190, 180, 180) ;
           outline: none;
        }
    }
    &__hearor{
        width: 100%;
        padding: 20px 0;
        display: flex;
        align-items: center;
        border-bottom: 2px solid rgb(227, 222, 222);
        &__active{
            padding: 10px;
            background-color: #016BFF;
            border-radius: 20px;
            text-decoration: none;
            color: aliceblue;
            margin-left: 30px;
            border: none;
            box-shadow:0px 0px 2px 2px rgb(150, 145, 145);
        }
        &__passive{
            border: none;
            padding: 10px;
            border-radius: 20px;
            background-color: #d5c8c8;
            text-decoration: none;
            color: #016BFF;
            margin-left: 30px;
        }
        &__chek{
            width: 300px;
            display: flex;
            align-items: center;
            margin-left: 300px;
            justify-content: space-between;
            input{
                width: 25px;
                height: 25px;
                border:2px solid black;
            }
        }

    }
    &__box{
      
        width: 100%;
        display: flex;
        flex-direction: column;
         align-items:flex-start;
        height: 550px;
        overflow-y: auto;
        &::-webkit-scrollbar{
            display: none;
        }
    }
    }
}

.user{
    display: flex;
    position: relative;
    &__img{
     width: 50px;
     height: 50px;
     margin-right: 20px;
     border-radius: 50%;
     border: 2px solid rgb(88, 88, 162);
    }
    &__id{
        position: absolute;
        bottom: 0;
        left:55px;
        font-size: 10px;
    }
}

