.Decloration{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;

    &__time{
        width: 35%;
        display: flex;
        padding: 20px;
        flex-direction: column;
        align-items: flex-start;
        border: 3px solid rgb(244, 243, 243);
        &__title{
            color: rgb(16, 16, 16);
        }
        &__vaqt{
              &__text{
                margin-left: -170px;
                margin-bottom:-30px;
              }
            &__date{
                width: 200px;
                margin-top: 20px;
                background-color:  #f6f1f1;
                font-size: 20px;
                border-radius: 5px;
                margin-right: 60px;
                height: 40px;
            }

            &__time{
                width: 100px;
                height: 40px;
                font-size: 20px;
                background-color:  #f6f1f1;
                border-radius: 5px;
            }
        }
         &__yonalish{
            display: flex;
            flex-wrap:wrap;
            margin-top: 30px;
            &__text1{
             margin-right: 120px;
            }
            &__text2{
                margin-right: 30px;
            }
            &__input{
                width: 220px;
                border-radius: 5px;
                font-size: 16px;
                height: 50px;
                margin-right: 40px;
                background-color:  #f6f1f1;
            }


        }
        &__tur{
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            &__text1{
                margin-right:190px;
            }
           &__btngroup{
            display: flex;
           margin-right: 60px;
           }
           &__link{
            width: 200px;
            height: 50px;
            border-radius: 5px;
            font-size: 18px;
            background-color:  #f6f1f1;
            padding-left:20px ;
           }
        }
    }
    &__tashkilot{
        width: 35%;
        padding: 20px;
        border: 3px solid rgb(244, 243, 243);
        &__name{
            display: flex;
            flex-wrap: wrap;
            &__text{
                margin-right: 180px;
            }
            &__input{
                width: 200px;
                height: 50px;
                margin-right: 30px;
                border-radius: 5px;
                padding-left: 20px;
                font-size: 16px;
                background-color:  #f6f1f1;
            }
        }
        &__number{
            display: flex;
            flex-wrap: wrap;
            &__text{
                margin-right: 145px;
            }
            &__input{
                width: 200px;
                height: 50px;
                margin-right: 30px;
                border-radius: 5px;
                padding-left: 20px;
                font-size: 16px;
                background-color:  #f6f1f1;
            }
        }
        
    }

    &__jismony{
        width: 35%;
        padding: 20px;
        border: 3px solid rgb(244, 243, 243);
        &__name{
            display: flex;
            flex-wrap: wrap;
            &__text{
                margin-right: 180px;
            }
            &__input{
                width: 200px;
                height: 50px;
                margin-right: 30px;
                border-radius: 5px;
                padding-left: 20px;
                font-size: 16px;
                background-color:  #f6f1f1;
            }
        }
        &__number{
            display: flex;
            flex-wrap: wrap;
            &__text{
                margin-right: 170px;
            }
            &__input{
                width: 200px;
                height: 50px;
                margin-right: 30px;
                border-radius: 5px;
                padding-left: 20px;
                font-size: 16px;
                background-color:  #f6f1f1;
            }
        }
        &__qoshimcha{
            display: flex;
            flex-direction: column;
            &__text{
                margin-right: auto;
            }
            &__input{
                width: 200px;
                height: 50px;
                margin-right: 30px;
                border-radius: 5px;
                padding-left: 20px;
                font-size: 16px;
                background-color:  #f6f1f1;
            }
        }
        
    }
    &__posts{
        width: 35%;
        padding: 20px;
        border: 3px solid rgb(244, 243, 243);
        &__post{
            width: 100%;
            text-align: start;
            &__input{
                width: 95%;
                height: 100px;
                background-color:  #ffffff;
                padding-left: 20px;
                border: none;
                outline: none;
                font-size: 30px;
                &::placeholder{
                    font-size: 30px;
                    color: #bdb7b7;
                }
            }
        }
        &__description{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            &__input{
                width: 100%;
                height: 55px;
                background-color:  #c4c1c1;
                padding-left: 20px;
                font-size: 18px;
                border-radius: 5px;
                border: none;
                outline: none;
            }
        }
        &__upload{
            width: 100%;
           display: flex;
           flex-direction: column;
           align-items: flex-start;
            &__file{
                width: 250px;
                height: 50px;
            }
        }
        &__mavzu{
            width: 100%;
           display: flex;
           flex-direction: column;
           align-items: flex-start;
            &__input{
                width:93%;
                height:200px;
                background-color:  #c4c1c1;
                padding: 20px;
                font-size: 20px;
                border: none;
                outline:none;
            }
        }

    }
}


.active{
    width: 100px;
    height: 50px;
    background-color: #006AFF;
}
.passive{
    width: 100px;
    height: 50px;
    background-color:  #f6f1f1;   
}