.Login{
    display: flex;  
    flex-direction: column;
    width:100%;
    align-items: center;
    &__title{
        width: 100%;
        font-size: 30px;
       margin-left: -200px;
       margin-bottom: 60px;
     margin-right:auto;
     color: #006AFF;
  
    }
    &__auth{
        margin-top:30px;
        position: relative;
        display: flex;  
        flex-direction: column;
        align-items: center;
        width:350px;
        border-radius: 10px;
        border: 2px solid rgb(193, 193, 229);
        padding: 20px;
        box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px, rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
        &__title{
            color: black;
            margin-right: auto;
            font-size: 30px;
            margin-bottom: 40px;
        }
        &__user{
           padding-left: 30px;
            width: 95%;
            height: 40px;
            border-radius: 5px;
            margin-bottom: 30px;
            font-size: 16px;
        }
        &__pass{
            padding-left: 30px;
            width: 95%;
            height: 40px;
            border-radius: 5px;
            margin-bottom: 30px;
            font-size: 16px;
        }
        &__btn{
           font-size: 16px;
            width: 100px;
            height: 40px;
            border: none;
            margin-bottom: 30px;
            border-radius: 5px;
            background-color: #006AFF;
            color: aliceblue;
        }
        // &__link{
        //     margin-top: 30px;
        //     margin-left: auto;
        //     text-decoration: none;
        //     color: #006AFF;
        // }
        
    }
}

.login_img{
     top: 136px;
    position: absolute;
    left: 20px;
}

.pass_img{
        top: 212px;
       position: absolute;
       left: 20px;
}