.Header{
    display: flex;
    width:100% ;
    padding: 20px 0px 20px 0px;
    align-items: center;
    justify-content: space-around;
    background-color:  #006AFF;
    &__logo{
        text-decoration: none;
        font-size: 30px;
        font-weight: 600;
        color: aliceblue;
    }
    &__input{
        width: 300px;
        height: 40px;
        border-radius: 7px;
        padding-left: 30px;
        background-color:#2e61a9 ;
        &:focus{
            outline: none;
        }
    }

    &__link{
        text-decoration: none;
        color: white;
        font-size: 19px;
    }
    &__btn{
        padding-top: 10px;
        width: 150px;
        text-align: center;
        height:40px;
        background-color:#FE8B37;
        color: aliceblue;
        text-decoration: none;
        border-radius: 7px;
    }
}