.Footer{
    display: flex;
    margin-top: auto;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding:10px 0;
    background-color: #095ACD;
    &__logo{
        font-size: 30px;
        font-weight: 600;
        text-decoration: none;
        color: white;
    }
    &__link{
        text-decoration: none;
        color: white;
        font-size: 19px;
    }
    &__icons{
        display: flex;
        width: 100px;
        justify-content: space-between;
    }
}