.card{
    margin-left: 30px;
    display: block;
    width:1000px;
    padding: 20px 50px;
    box-shadow: 0 0 0 3px rgba(68, 68, 150, 0.285);
    background-color: aliceblue;
    &__cantrol{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__title{
            width: 60%;
            text-align:start;
            font-size: 25px;
            
        }
        &__false{
            margin-right: -80px;
              border: none;
              height: 40px;
              background-color: rgb(66, 66, 221);
              color: white;
              font-size: 20px;
              border-radius: 5px;
              box-shadow: 0 0 0 2px rgb(210, 171, 171);
              &:active{
                background-color: rgb(118, 118, 202);
              
            }
        }
        &__true{
            border: none;
            height: 40px;
            background-color: rgb(206, 206, 220);
            color: rgb(65, 34, 207);
            font-size: 20px;
            border-radius: 5px;
            box-shadow: 0 0 0 2px rgb(210, 171, 171);
            &:active{
                background-color: rgb(118, 118, 202);
              
            }
        }
    }
    &__info{
        display: flex;
        align-items: center;
        justify-content: space-around;
        &__item{
            list-style: none;
            color: blue;
        }
    }
}