.Home{
    width: 100%;
   
    &__nav{
        display: flex;
        justify-content: center;
        background-color: #006AFF;
        align-items: center;
        padding: 100px 0;
        width: 100%;
        &__title{
            font-size: 50px;
            width: 50%;
            color: aliceblue;
        }
    }
    &__sort{
        display: flex;
        width: 750px;
        position: absolute;
       margin-top: -30px;
       margin-left:30%;
        &__date{
            width: 150px;
            height: 46px;
            background-color: white;
            border-radius: 5px 0px 0px 5px;
        }
        &__bolim{
            width: 150px;
            height: 50px;
            background-color: white;
           
          
        }
        &__turi{
            width: 150px;
            height: 50px;
            background-color: white;
        }
        &__name{
            width: 150px;
            height: 50px;
            background-color: white;
        }
        &__btn{
            width: 100px;
            background-color: #0e3367;
            border: #006AFF;
            color: white;
            border-radius: 0px 5px 5px 0px;
        }
    }
    &__blok{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding: 100px 0;
        justify-content: space-around;
    }
}






.cards{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    background-color: white;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    border-radius: 10px;
    margin: 30px 0;
    &__img{
        border-radius: 10px 10px 0px 0px;
        width: 100%;
        height:350px;
    }
    &__title{
        width: 80%;
    }
    &__icons{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 50%;
        padding-right: 10px;
    }
    &__box{
        display: flex;
        justify-content: space-between;
        width: 100%;
        position: relative;
        // background-color: #006AFF;
        &__icons{
      
            &__icon{
                margin:5px;
                display: flex;
                align-items: center;

                list-style: none;
                img{
                    display:inline-block;
                  margin-right: 10px;
                }
            }
        }
    }
}

.add-btn{
    background-color: #006AFF;
    width: 150px;
    height: 45px;
    border: #006AFF;
    border-radius: 3px;
    display:inline-block;
    margin-bottom: 50px;
  
}