.Banner{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 70px;
    margin-bottom: 50px;
    &__list{
        width: 35%;
        height: 300px;
        display: flex;
        position: relative;
        background-color: #055BD3;
        &__title{
            width: 300px;
            color: aliceblue;
        }
        &__img{
            position: absolute;
            right: 0;
            bottom: 0px;
          
        }
    }
}